import { Component } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms'
import { fullNameValidators } from './fullName.validators';
import {NG_VALIDATORS} from '@angular/forms';

@Component({
  selector : 'signup11',
  templateUrl : './app-signup.component.html',
  styleUrls : ['./app-signup.component.css'],
  providers : [{
    provide: NG_VALIDATORS,
      useValue: fullNameValidators,
      multi: true}]
})
export class SignupComponent{
  form = new FormGroup({
    account : new FormGroup({
      fullName : new FormControl('', 
    [
      Validators.required,
      Validators.minLength(3),
      Validators.maxLength(20),
      fullNameValidators.mustHaveSpaceBetweenNames],
      fullNameValidators.shouldBeUnique
      ),
    password : new FormControl('', Validators.required)
    })
  });
testFunction() {
  console.log('Test checking');
}

  get fullName(){
    return this.form.get('account.fullName');
  }
  get password(){
    return this.form.get('account.password');
  }
  login() {
    this.form.setErrors({
      invalidFullName : true
    });
  }
}