import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { TitleService } from './app-title.service';

@Component({
  selector: 'new-title',
  templateUrl: './app-new-title.component.html'
})
export class NewTitleComponent implements OnInit  {
  posts : any[];
  constructor(private service : TitleService) {
  }

  ngOnInit() {
    this.service.getPosts().subscribe( response => {
      this.posts = response;
    })
  }
  addTitle(input : HTMLInputElement) {
    let post = { title : input.value };
    input.value=''
    this.service.addPost(post).subscribe( response => {
      post['id'] = response['id'];
      this.posts.splice(0,0,post);
    }, (error: Response) => {
      if( error.status === 400)
        alert("Post could not be added.");
      else
        alert("Unexpected Add Post Error!!!");
      console.log(error);
    })
  }

  updateTitle(post) {
    this.service.updatePost(post).subscribe( response => {
      post.title = "jello";
    }, (error: Response) => {
      if( error.status === 404)
        alert("Post does not exists.");
      else
        alert("Unexpected Update Post Error!!!");
      console.log(error);
    })
  }

  deleteTitle(post) {
    this.service.deletePost(post.id).subscribe( response => {
      let index = this.posts.indexOf(post);
      this.posts.splice(index,1);
    }, (error: Response) => {
      if( error.status === 404)
        alert("Post Already Deleted.");
      else
        alert("Unexpected Delete Post Error!!!");
      console.log(error);
    })
  }
}

