import { AbstractControl, ValidationErrors} from '@angular/forms'

export class fullNameValidators{
  static mustHaveSpaceBetweenNames(control: AbstractControl) : ValidationErrors | null {
    if((control.value as string).indexOf(' ') < 0)
      return {mustHaveSpaceBetweenNames: 'Please have space between First Name and Last Name'};
      
    return null;
  }
  static shouldBeUnique(control:AbstractControl) : Promise <ValidationErrors | null> {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        if(control.value == "bot bot")
          resolve ({ shouldBeUnique : "User Name already taken." });
        else
         resolve (null);
      }, 2000);
    });
    return null;
  }
}